    <el-form  :model="formdata" label-width="80px" label-position="top">
      <el-form-item label="用户名">
        <el-input v-model="formdata.username"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input type="password" v-model="formdata.password"></el-input>
      </el-form-item>
      <div class="button-wrap"><el-button type="primary" round @click="handleSignin">登录</el-button></div>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'Signin',
  data () {
    return {
     formdata: {
       username: '',
       password: '',
     },
    };
  },
  methods: {
    handleSignin () {
      window.loading = this.$loading();
      const that = this;
      window.firebase.auth().setPersistence(firebase.auth.Auth.Persistence.SESSION)
  .then(function() {
    return window.firebase.auth().signInWithEmailAndPassword(that.formdata.username, that.formdata.password).catch(function(error) {
        // Handle Errors here.
        var errorCode = error.code;
        var errorMessage = error.message;
        // [START_EXCLUDE]
        if (errorCode === 'auth/wrong-password') {
          alert('Wrong password.');
        } else {
          alert(errorMessage);
        }
        loading.close();
      });
  });

    },
  },
}
</script>

<style scoped>
.button-wrap{
  display: flex;
  justify-content: center;
}
.content{
  padding: 10px 10px;
}
</style>
